import React, { useState, useRef, useEffect, useMemo, useCallback } from 'react';
import gsap from 'gsap';
import { Draggable } from 'gsap/Draggable';

if (typeof window !== 'undefined') {
  gsap.registerPlugin(Draggable);
}

// Configuration constants
const CONFIG = {
  baseScale: 64,
  scaleMultiplier: 1.5,
  snapbackDelay: 100,
  snapbackDuration: 0.15,
  trailOpacity: 0.6,
  glowIntensity: 20,
  containerSizeRatio: 0.6,
  radiusOffset: 0.85,
  maxExpansion: 1.4,
  expansionBase: 1.5
} as const;

// Enhanced Emoji mappings for different positions with more variety
const EMOJI_MAP = {
  topRight: ['', '', '', '', '', '', '', '', '', ''],
  topLeft: ['', '', '', '', '', '', '', '', '', ''],
  bottomLeft: ['', '', '', '', '', '', '', '', '', ''],
  bottomRight: ['', '', '', '', '', '', '', '', '', ''],
  right: ['', '', '', '', ''],
  left: ['', '', '', '', ''],
  top: ['', '', '', '', ''],
  bottom: ['', '', '', '', ''],
  center: ['', '', '', '', '']
} as const;

// Emotion words for different quadrants
const EMOTION_WORDS = {
  topRight: ['Excited', 'Ecstatic', 'Energetic', 'Thrilled', 'Joyful', 'Elated'],
  topLeft: ['Angry', 'Frustrated', 'Tense', 'Anxious', 'Stressed', 'Furious'],
  bottomRight: ['Content', 'Satisfied', 'Calm', 'Relaxed', 'Peaceful', 'Serene'],
  bottomLeft: ['Sad', 'Bored', 'Tired', 'Depressed', 'Melancholy', 'Dejected']
} as const;

interface EmojiGridMapperProps {
  onEmojiSelect?: (emoji: any) => void;
  onGridChange?: (grid: any) => void;
}

const EmojiGridMapper: React.FC<EmojiGridMapperProps> = ({ onEmojiSelect }) => {
  // Core state
  const [position, setPosition] = useState({ x: 0, y: 0 });
  const [isDragging, setIsDragging] = useState(false);
  const [isReturning, setIsReturning] = useState(false);
  const [dragPath, setDragPath] = useState<{ x: number, y: number }[]>([]);
  const [containerSize, setContainerSize] = useState({ width: 400, height: 400 });
  const [returnProgress, setReturnProgress] = useState(0);

  // Refs
  const containerRef = useRef<HTMLDivElement>(null);
  const emojiRef = useRef<HTMLDivElement>(null);
  const pathRef = useRef<SVGPathElement>(null);

  // Utility functions
  const polarToCartesian = useCallback((x: number, y: number) => {
    const distance = Math.sqrt(x * x + y * y);
    const clampedDistance = Math.min(distance, 1);
    if (distance === 0) return { x: 0, y: 0 };
    
    const angle = Math.atan2(y, x);
    return {
      x: clampedDistance * Math.cos(angle),
      y: clampedDistance * Math.sin(angle)
    };
  }, []);

  const screenToUnit = useCallback((screenX: number, screenY: number) => {
    const radius = Math.min(containerSize.width, containerSize.height) / 2;
    const unitX = screenX / radius;
    const unitY = -screenY / radius;
    return polarToCartesian(unitX, unitY);
  }, [containerSize, polarToCartesian]);



  // Calculate logarithmic circle expansion
  const getCircleScale = useCallback(() => {
    const distance = Math.sqrt(position.x * position.x + position.y * position.y);
    const logScale = 1 + (Math.log(1 + distance * CONFIG.expansionBase) / Math.log(1 + CONFIG.expansionBase)) * (CONFIG.maxExpansion - 1);
    return Math.min(logScale, CONFIG.maxExpansion);
  }, [position.x, position.y]);

  // Dynamic styling functions with 4-color gradient based on quadrants
  const backgroundColor = useMemo(() => {
    const distance = Math.sqrt(position.x * position.x + position.y * position.y);
    const intensity = Math.min(1, distance);
    
    let baseColor = { r: 0, g: 0, b: 0 };
    
    if (position.x >= 0 && position.y >= 0) {
      baseColor = { r: 255, g: 215, b: 0 };
    } else if (position.x < 0 && position.y >= 0) {
      baseColor = { r: 0, g: 100, b: 255 };
    } else if (position.x < 0 && position.y < 0) {
      baseColor = { r: 128, g: 0, b: 128 };
    } else {
      baseColor = { r: 255, g: 50, b: 50 };
    }
    
    const r = Math.round(baseColor.r * intensity * 0.3);
    const g = Math.round(baseColor.g * intensity * 0.3);
    const b = Math.round(baseColor.b * intensity * 0.3);
    
    return `rgb(${r}, ${g}, ${b})`;
  }, [position.x, position.y]);

  // Get current emotion words
  const getCurrentEmotionWords = useCallback(() => {
    if (position.x >= 0 && position.y >= 0) return EMOTION_WORDS.topRight;
    if (position.x < 0 && position.y >= 0) return EMOTION_WORDS.topLeft;
    if (position.x < 0 && position.y < 0) return EMOTION_WORDS.bottomLeft;
    return EMOTION_WORDS.bottomRight;
  }, [position.x, position.y]);

  // Enhanced emoji selection with variety
  const getEmoji = useCallback((x: number, y: number) => {
    const distance = Math.sqrt(x * x + y * y);
    const index = Math.floor(distance * 9) % 10;
    
    if (x > 0.6 && y > 0.6) return EMOJI_MAP.topRight[index] || EMOJI_MAP.topRight[0];
    if (x < -0.6 && y > 0.6) return EMOJI_MAP.topLeft[index] || EMOJI_MAP.topLeft[0];
    if (x < -0.6 && y < -0.6) return EMOJI_MAP.bottomLeft[index] || EMOJI_MAP.bottomLeft[0];
    if (x > 0.6 && y < -0.6) return EMOJI_MAP.bottomRight[index] || EMOJI_MAP.bottomRight[0];
    if (x > 0.2) return EMOJI_MAP.right[index % 5] || EMOJI_MAP.right[0];
    if (x < -0.2) return EMOJI_MAP.left[index % 5] || EMOJI_MAP.left[0];
    if (y > 0.2) return EMOJI_MAP.top[index % 5] || EMOJI_MAP.top[0];
    if (y < -0.2) return EMOJI_MAP.bottom[index % 5] || EMOJI_MAP.bottom[0];
    return EMOJI_MAP.center[index % 5] || EMOJI_MAP.center[0] || '';
  }, []);

  const getEmojiScale = useCallback((x: number, y: number) => {
    const distance = Math.sqrt(x * x + y * y);
    let baseScale = 1 + (distance * CONFIG.scaleMultiplier);
    
    if (isReturning) {
      const maxScale = 1 + (distance * CONFIG.scaleMultiplier);
      baseScale = maxScale - (returnProgress * (maxScale - 1));
    }
    
    return baseScale;
  }, [isReturning, returnProgress]);

  // Calculate percentages for axis display
  const xPercentage = Math.round(Math.abs(position.x) * 100);
  const yPercentage = Math.round(Math.abs(position.y) * 100);

  // Update container size on window resize
  useEffect(() => {
    const updateSize = () => {
      if (containerRef.current) {
        const maxSize = Math.min(
          window.innerWidth * CONFIG.containerSizeRatio, 
          (window.innerHeight - 200) * CONFIG.containerSizeRatio
        );
        const size = Math.max(250, Math.min(400, maxSize));
        setContainerSize({ width: size, height: size });
      }
    };

    updateSize();
    window.addEventListener('resize', updateSize);
    return () => window.removeEventListener('resize', updateSize);
  }, []);

  // Update trail path
  useEffect(() => {
    if (!pathRef.current || dragPath.length < 2) return;
    
    let pathData = `M${dragPath[0].x},${dragPath[0].y}`;
    dragPath.forEach((point, index) => {
      if (index === 0) return;
      pathData += ` L${point.x},${point.y}`;
    });
    
    pathRef.current.setAttribute('d', pathData);
  }, [dragPath]);

  // Setup GSAP dragging functionality
  useEffect(() => {
    if (!emojiRef.current) return;

    let currentDragPath: { x: number, y: number }[] = [];
    const radius = Math.min(containerSize.width, containerSize.height) / 2;

    const dragInstance = Draggable.create(emojiRef.current, {
      type: "x,y",
      bounds: {
        minX: -radius,
        maxX: radius,
        minY: -radius,
        maxY: radius
      },
      onDragStart: function() {
        setIsDragging(true);
        setIsReturning(false);
        setReturnProgress(0);
        currentDragPath = [{ x: 0, y: 0 }];
        setDragPath([...currentDragPath]);
      },
      onDrag: function() {
        // Constrain to circle
        const distance = Math.sqrt(this.x * this.x + this.y * this.y);
        if (distance > radius) {
          const angle = Math.atan2(this.y, this.x);
          const constrainedX = radius * Math.cos(angle);
          const constrainedY = radius * Math.sin(angle);
          gsap.set(this.target, { x: constrainedX, y: constrainedY });
          this.x = constrainedX;
          this.y = constrainedY;
        }

        const unitCoords = screenToUnit(this.x, this.y);
        setPosition(unitCoords);
        
        currentDragPath.push({ x: this.x, y: this.y });
        setDragPath([...currentDragPath]);

        const currentEmoji = getEmoji(unitCoords.x, unitCoords.y);
        const emotionWords = getCurrentEmotionWords();
        onEmojiSelect?.({
          emoji: currentEmoji,
          position: unitCoords,
          emotion: emotionWords[0],
          valence: unitCoords.x,
          arousal: unitCoords.y
        });
      },
      onDragEnd: function() {
        setIsDragging(false);
        setIsReturning(true);
        
        setTimeout(() => {
          const reversePath = [...currentDragPath].reverse();
          const timeline = gsap.timeline();
          
          reversePath.forEach((point, index) => {
            timeline.to(this.target, {
              x: point.x,
              y: point.y,
              duration: 0.02,
              ease: "none",
              onUpdate: () => {
                const progress = index / (reversePath.length - 1);
                setReturnProgress(progress);
                const unitCoords = screenToUnit(this.x, this.y);
                setPosition(unitCoords);
              }
            }, index * 0.02);
          });
          
          timeline.to(this.target, {
            x: 0,
            y: 0,
            duration: CONFIG.snapbackDuration,
            ease: "power2.out",
            onUpdate: () => {
              setReturnProgress(1);
              const unitCoords = screenToUnit(this.x, this.y);
              setPosition(unitCoords);
            }
          });
          
          timeline.call(() => {
            setDragPath([]);
            setPosition({ x: 0, y: 0 });
            setIsReturning(false);
            setReturnProgress(0);
          });
        }, CONFIG.snapbackDelay);
      }
    })[0];

    return () => {
      dragInstance.kill();
    };
  }, [containerSize, screenToUnit, getEmoji, getCurrentEmotionWords, onEmojiSelect]);

  const scale = getEmojiScale(position.x, position.y);
  const circleScale = getCircleScale();
  const currentEmotionWords = getCurrentEmotionWords();

  return (
    <div className="h-full bg-black flex items-center justify-center relative overflow-hidden p-4">
      {/* Animated background particles */}
      <div className="absolute inset-0 opacity-10">
        {[...Array(40)].map((_, i) => (
          <div
            key={i}
            className="absolute rounded-full animate-pulse"
            style={{
              left: `${Math.random() * 100}%`,
              top: `${Math.random() * 100}%`,
              width: `${1 + Math.random() * 3}px`,
              height: `${1 + Math.random() * 3}px`,
              backgroundColor: i % 4 === 0 ? '#FFD700' : i % 4 === 1 ? '#0064FF' : i % 4 === 2 ? '#800080' : '#FF3232',
              animationDelay: `${Math.random() * 4}s`,
              animationDuration: `${2 + Math.random() * 3}s`
            }}
          />
        ))}
      </div>

      {/* Axis indicators */}
      {(isDragging || position.x !== 0 || position.y !== 0) && (
        <>
          {/* Valence indicator */}
          <div className="absolute top-2 sm:top-4 left-1/2 transform -translate-x-1/2 text-white text-xs sm:text-sm font-mono bg-black/80 px-3 sm:px-6 py-2 sm:py-3 rounded-full backdrop-blur-sm border border-white/30 shadow-lg">
            <div className="text-center">
              <div className="text-sm sm:text-lg font-bold text-yellow-300">VALENCE</div>
              <div className="text-xs text-white/80 mb-1 sm:mb-2">Emotional Positivity</div>
              <div className="flex items-center gap-1 sm:gap-2">
                <span className="text-red-300 text-xs">Negative</span>
                <div className="flex-1 bg-white/20 rounded-full h-1.5 sm:h-2 mx-1 sm:mx-2 min-w-[60px] sm:min-w-[80px]">
                  <div 
                    className={`h-1.5 sm:h-2 rounded-full transition-all duration-200 ${position.x >= 0 ? 'bg-green-400' : 'bg-red-400'}`}
                    style={{ width: `${xPercentage}%`, marginLeft: position.x < 0 ? `${100 - xPercentage}%` : '0' }}
                  />
                </div>
                <span className="text-green-300 text-xs">Positive</span>
              </div>
              <div className="text-sm sm:text-lg font-bold mt-1">{position.x > 0 ? '+' : position.x < 0 ? '-' : ''}{xPercentage}%</div>
            </div>
          </div>

          {/* Arousal indicator */}
          <div className="absolute right-2 sm:right-4 top-1/2 transform -translate-y-1/2 text-white text-xs sm:text-sm font-mono bg-black/80 px-3 sm:px-6 py-2 sm:py-3 rounded-full backdrop-blur-sm border border-white/30 shadow-lg">
            <div className="text-center">
              <div className="text-sm sm:text-lg font-bold text-blue-300">AROUSAL</div>
              <div className="text-xs text-white/80 mb-1 sm:mb-2">Energy Level</div>
              <div className="flex flex-col items-center gap-1">
                <span className="text-orange-300 text-xs">High</span>
                <div className="bg-white/20 rounded-full w-1.5 sm:w-2 h-16 sm:h-20 mx-2 relative">
                  <div 
                    className={`w-1.5 sm:w-2 rounded-full transition-all duration-200 ${position.y >= 0 ? 'bg-orange-400' : 'bg-blue-400'}`}
                    style={{ 
                      height: `${yPercentage}%`, 
                      position: 'absolute',
                      bottom: position.y >= 0 ? '50%' : 'auto',
                      top: position.y < 0 ? '50%' : 'auto'
                    }}
                  />
                </div>
                <span className="text-blue-300 text-xs">Low</span>
              </div>
              <div className="text-sm sm:text-lg font-bold mt-1 sm:mt-2">{position.y > 0 ? '+' : position.y < 0 ? '-' : ''}{yPercentage}%</div>
            </div>
          </div>
        </>
      )}

      {/* Floating emotion words */}
      {isDragging && (
        <div className="absolute top-1/2 left-1/4 transform -translate-x-1/2 -translate-y-1/2 pointer-events-none">
          {currentEmotionWords.slice(0, 4).map((word, index) => (
            <div
              key={word}
              className="text-white/60 text-sm sm:text-lg font-light animate-pulse"
              style={{
                position: 'absolute',
                left: `${index * 20}px`,
                top: `${index * 20 - 40}px`,
                animationDelay: `${index * 0.5}s`,
                animationDuration: '2s'
              }}
            >
              {word}
            </div>
          ))}
        </div>
      )}

      <div
        ref={containerRef}
        className="relative rounded-full select-none shadow-2xl transition-transform duration-300 ease-out"
        style={{
          width: containerSize.width * circleScale,
          height: containerSize.height * circleScale,
          backgroundColor,
          transition: isDragging ? 'background-color 0.3s ease' : 'background-color 0.3s ease, transform 0.3s ease',
          boxShadow: `0 0 ${Math.min(1, Math.sqrt(position.x * position.x + position.y * position.y)) * 40}px rgba(255,255,255,0.3), 
                     inset 0 0 40px rgba(255,255,255,0.1)`,
          overflow: 'hidden'
        }}
      >
        {/* Outer glow ring */}
        <div 
          className="absolute inset-0 rounded-full border-2 border-white/30"
          style={{
            filter: 'blur(1px)',
            animation: isDragging ? 'pulse 1s infinite' : 'none',
            boxShadow: isDragging ? '0 0 25px rgba(255,255,255,0.4)' : 'none'
          }}
        />

        {/* Quadrant labels */}
        <div className="absolute inset-2 sm:inset-4 pointer-events-none">
          <div className="absolute top-1 sm:top-2 right-1 sm:right-2 text-yellow-300/50 text-xs font-bold">High Energy<br/>Positive</div>
          <div className="absolute top-1 sm:top-2 left-1 sm:left-2 text-blue-300/50 text-xs font-bold">High Energy<br/>Negative</div>
          <div className="absolute bottom-1 sm:bottom-2 left-1 sm:left-2 text-purple-300/50 text-xs font-bold">Low Energy<br/>Negative</div>
          <div className="absolute bottom-1 sm:bottom-2 right-1 sm:right-2 text-red-300/50 text-xs font-bold">Low Energy<br/>Positive</div>
        </div>

        {/* Trail SVG */}
        <svg 
          className="absolute inset-0 w-full h-full pointer-events-none z-10"
          style={{ opacity: isDragging ? CONFIG.trailOpacity : 0, transition: 'opacity 0.3s ease' }}
        >
          <defs>
            <filter id="glow">
              <feGaussianBlur stdDeviation="2" result="coloredBlur"/>
              <feMerge> 
                <feMergeNode in="coloredBlur"/>
                <feMergeNode in="SourceGraphic"/>
              </feMerge>
            </filter>
            <linearGradient id="trailGradient" x1="0%" y1="0%" x2="100%" y2="100%">
              <stop offset="0%" stopColor="#ffffff" stopOpacity="0.8"/>
              <stop offset="50%" stopColor="#ffd700" stopOpacity="0.6"/>
              <stop offset="100%" stopColor="#ffffff" stopOpacity="0.2"/>
            </linearGradient>
          </defs>
          <path 
            ref={pathRef}
            d="" 
            stroke="url(#trailGradient)" 
            strokeWidth="4" 
            fill="none"
            strokeLinecap="round"
            strokeLinejoin="round"
            filter="url(#glow)"
            transform={`translate(${(containerSize.width * circleScale) / 2}, ${(containerSize.height * circleScale) / 2})`}
          />
        </svg>
        
                {/* Emoji with dynamic effects */}
        <div
          ref={emojiRef}
          className="absolute cursor-grab z-30 select-none flex items-center justify-center"
          style={{
            fontSize: `${CONFIG.baseScale * scale}px`,
            filter: `drop-shadow(0 3px 8px rgba(0,0,0,0.4)) drop-shadow(0 0 ${scale * 10}px rgba(255,255,255,0.6))`,
            transition: isReturning ? 'transform 0.1s ease-out' : 'none',
            transform: `rotate(${isDragging ? position.x * 10 : 0}deg) scale(${isDragging ? 1.1 : 1})`,
            width: `${CONFIG.baseScale * scale}px`,
            height: `${CONFIG.baseScale * scale}px`,
            lineHeight: 1,
            userSelect: 'none',
            WebkitUserSelect: 'none',
            left: '50%',
            top: '50%',
            marginLeft: `-${(CONFIG.baseScale * scale) / 2}px`,
            marginTop: `-${(CONFIG.baseScale * scale) / 2}px`
          }}
        >
          {getEmoji(position.x, position.y) || ''}
        </div>

        {/* Center dot - only show when not dragging and at center */}
        {!isDragging && position.x === 0 && position.y === 0 && (
          <div className="absolute top-1/2 left-1/2 w-2 sm:w-3 h-2 sm:h-3 bg-white/60 rounded-full transform -translate-x-1/2 -translate-y-1/2 z-20">
            <div className="absolute inset-0 bg-white/30 rounded-full animate-ping" />
          </div>
        )}
      </div>

      {/* Instructions */}
      {!isDragging && position.x === 0 && position.y === 0 && (
        <div className="absolute bottom-4 sm:bottom-8 left-1/2 transform -translate-x-1/2 text-white/70 text-center animate-fade-in">
          <div className="text-lg sm:text-xl mb-2 sm:mb-3 animate-pulse"> Explore Your Emotions</div>
          <div className="text-xs sm:text-sm mb-1 sm:mb-2">Drag to navigate the emotional spectrum</div>
          <div className="text-xs text-white/50">
            Valence (left-right)  Arousal (up-down)
          </div>
        </div>
      )}

      {/* Mood indicator when dragging */}
      {isDragging && (
        <div className="absolute bottom-4 sm:bottom-8 left-1/2 transform -translate-x-1/2 text-white text-center animate-fade-in">
          <div className="text-xl sm:text-2xl mb-1 sm:mb-2">{currentEmotionWords[0]}</div>
          <div className="text-xs sm:text-sm text-white/70">
            Current Mood State
          </div>
        </div>
      )}
    </div>
  );
};

export default EmojiGridMapper; 